export enum TimeframeType {
  DAY = 'day',
  WEEK = 'week',
  MONTH = 'month',
  SEASON = 'season',
  INCEPTION = 'inception',
}

const TimeframeDays = {
  [TimeframeType.DAY]: 1,
  [TimeframeType.WEEK]: 7,
  [TimeframeType.MONTH]: 30,
  [TimeframeType.SEASON]: 120,
  [TimeframeType.INCEPTION]: 365,
};

const TimeframeText = {
  [TimeframeType.DAY]: 'Past 1 Day',
  [TimeframeType.WEEK]: 'Past 1 Week',
  [TimeframeType.MONTH]: 'Past 1 Month',
  [TimeframeType.SEASON]: 'Past 3 Months',
  [TimeframeType.INCEPTION]: 'All Time',
};

export const getTimeIntervalText = (timeRage: TimeframeType, data: any[]) => {
  return timeRage === TimeframeType.INCEPTION
    ? data && data.length > 0
      ? `since ${timestampToUTCString(data[0].timestamp_sec, 'MonthDayYear')}`
      : TimeframeText[timeRage]
    : TimeframeText[timeRage];
};

export const TimeframeInput = [
  { text: '1D', value: TimeframeType.DAY },
  { text: '1W', value: TimeframeType.WEEK },
  { text: '1M', value: TimeframeType.MONTH },
  { text: '3M', value: TimeframeType.SEASON },
  { text: 'ALL', value: TimeframeType.INCEPTION },
];

const mergeByTime = (a1: any[], a2: any[]) =>
  a1.map((itm) => ({
    ...a2.find((item) => item.timestamp_sec === itm.timestamp_sec && item),
    ...itm,
  }));

export const find_x_days_ago = (data: any[], days: number) => {
  if (data.length > 0) {
    const earlist = data[data.length - 1].timestamp_sec - 86400 * days;
    const index = data.findIndex((item) => item.timestamp_sec >= earlist);
    return data.slice(index, data.length);
  }
  return data;
};

export const calculate_backtest_analysis_apy = (data: any[]) => {
  if (data.length > 0) {
    const days_count =
      (data[data.length - 1].timestamp_sec - data[0].timestamp_sec) / 86400;
    const period = 365 / days_count;
    return Math.pow(data[data.length - 1].percentage + 1, period) - 1;
  }
  return 0;
};

export const calculate_return = (data: any[]) => {
  if (data.length > 0) {
    return (
      (data[data.length - 1].share_price - data[0].share_price) /
      data[0].share_price
    );
  }
  return null;
};

export const calculate_timeframe_apy = (
  data: any[],
  timeframe: TimeframeType
) => {
  const returnData = calculate_return(data);
  if (returnData !== null) {
    const days =
      (data[data.length - 1].timestamp_sec - data[0].timestamp_sec) / 86400;
    return Math.exp(Math.log(returnData + 1) / (days / 365)) - 1;
  }
  return null;
};

/* token A stable coin, token B unit coin */
export const getReturnData = (
  assetPrices: { [token: string]: any[] },
  homoraPerformance: any[],
  strategyId: string,
) => {
  let return_data = [];
  // TODO: `_getEvmStrategyInfo` has been accelerated so much that when `getReturnData` is invoked
  //  `assetPrices` retrieved from AWS may still be empty which causes the page to crash.
  //  Although `fetchGraphdata` has also been optimized by concurrency, we should still set up proper dependencies
  //  such that `getReturnData` doesn't start until `assetPrices` and `homoraPerformance` are fetched.
  // if (!assetPrices) {
  //   console.error('assetPrices empty', tokenA, tokenB);
  // }
  // if (!homoraPerformance) {
  //   console.error('homoraPerformance empty', tokenA, tokenB);
  // }
  if (isNotEmpty(assetPrices) && isNotEmpty(homoraPerformance)) {
    let asset: any[];
    let performance: any[];
    // if (token in ERC20TokenMap && tokenB in tickerMap) {
    //   asset = assetPrices[tickerMap[tokenB]];
    //   performance = homoraPerformance[ERC20TokenMap[tokenA]];
    // } else if (token in ERC20TokenMap && tokenA in tickerMap) {
    //   asset = assetPrices[tickerMap[tokenA]];
    //   performance = homoraPerformance[ERC20TokenMap[tokenB]];
    // } else {
    //   // console.error(`Wrong tokens ${tokenA} ${tokenB} in getReturnData`);
    //   return {
    //     day: [],
    //     week: [],
    //     month: [],
    //     season: [],
    //     inception: [],
    //   };
    // }
    asset = assetPrices['AVAX'];
    performance = homoraPerformance[strategyId];
    const initial_price = performance[0].price;
    const percentage_data = performance.map((itm: any) => ({
      timestamp_sec: itm.timestamp_sec,
      percentage: (itm.price - initial_price) / initial_price,
      share_price: itm.price,
    }));
    return_data = mergeByTime(percentage_data, asset);
  }
  return {
    day: find_x_days_ago(return_data, 1),
    week: find_x_days_ago(return_data, 7),
    month: find_x_days_ago(return_data, 30),
    season: find_x_days_ago(return_data, 120),
    inception: return_data,
  };
};

export const getPerformanceData = (performance: any, share_price: any) => {
  // when user first time deposition/open a position. Database doesnt have user's data, we hard code one datapoint to show user's deposit.
  if (!performance || performance.length <= 0) {
    performance = [
      {
        timestamp_sec: Math.floor(Date.now() / 1000),
        user_share_usd_value: Number(share_price ?? 0),
      },
    ];
  }

  const updatePerformanceData = (list: any[]) => {
    //calculation: (curent value - first value) / first value
    //if first value is 0, move on to find the first none zero value and do the same calculation above
    let initial_price = list[0].user_share_usd_value;
    return list.map((itm: any) => {
      let result;
      if (initial_price === 0) {
        result = {
          timestamp_sec: itm.timestamp_sec,
          percentage: 0,
          share_price: itm.user_share_usd_value,
          share_price_difference: 0,
        };
      } else {
        result = {
          timestamp_sec: itm.timestamp_sec,
          percentage:
            (itm.user_share_usd_value - initial_price) / initial_price,
          share_price: itm.user_share_usd_value,
          share_price_difference: itm.user_share_usd_value - initial_price,
        };
      }
      //to find the first none zero value
      if (initial_price === 0 && itm.user_share_usd_value !== 0) {
        initial_price = itm.user_share_usd_value;
      }
      return result;
    });
  };
  return {
    //get data from different timeframes
    day: updatePerformanceData(find_x_days_ago(performance, 1)),
    week: updatePerformanceData(find_x_days_ago(performance, 7)),
    month: updatePerformanceData(find_x_days_ago(performance, 30)),
    season: updatePerformanceData(find_x_days_ago(performance, 120)),
    inception: updatePerformanceData(performance),
  };
};

function isNotEmpty(obj: Object) {
  if (obj) {
    return Object.keys(obj).length !== 0;
  } else return false;
}

/* timeGap unit second, 1800 = 30mins */
export const bucketTimestamp = (data: any[], timeGap: number) => {
  if (data && data.length > 1) {
    const list: any[] = [];
    let time_counter = Math.floor(data[0].timestamp_sec / timeGap) * timeGap;
    data.forEach((itm: any, index: number) => {
      if (data[index].timestamp_sec > time_counter + timeGap) {
        time_counter =
          Math.floor(data[index].timestamp_sec / timeGap) * timeGap;
      }
      if (index + 1 === data.length) {
        itm.timestamp_sec = time_counter;
        list.push(itm);
      } else if (
        data[index].timestamp_sec >= time_counter &&
        data[index].timestamp_sec < time_counter + timeGap &&
        data[index + 1].timestamp_sec >= time_counter + timeGap
      ) {
        itm.timestamp_sec = time_counter;
        list.push(itm);
      }
    });
    return list;
  }
  return data;
};
