import { useMediaQuery } from '@mui/material';
import { forwardRef, useRef, useState } from 'react';
import { Box, ButtonGroups, NumberCounter } from '../../../..';
import {
  getTimeIntervalText,
  TimeframeInput,
  TimeframeType,
} from '../../../../../../helpers/graph';
import { numberToPercentage } from '../../../../../../helpers/utilities';
import { LineGraph } from '../../../../Charts/LineGraph';
interface UserPerformanceProps {
  color: any;
  data: any;
  payload: any;
  token: any;
}

const tooltipInfo = (timeRage: TimeframeType) => {
  return {
    day: timeRage === TimeframeType.WEEK,
    hours: true,
    value: [
      {
        color: '#85DAE5',
        text: 'Net Value',
        value: 'share_price',
        prefix: '$',
      },
      {
        color: '#85DAE5',
        text: 'Change',
        value: 'share_price_difference',
        changingRate: 'percentage',
        prefix: '$',
        colorChanging: true,
        addSignForValue: true,
      },
    ],
  };
};

const NetValueChanged = ({
  share_price_difference,
  percentage,
}: {
  share_price_difference: number;
  percentage: number;
}) => (
  <span
    className={`span-1 ${share_price_difference >= 0 ? 'positive' : 'negative'
      }`}
  >
    {share_price_difference > 0 && '+'}
    {share_price_difference < 0 && '-'}$
    {Math.abs(share_price_difference).toFixed(2)}
    <NumberCounter
      value={percentage}
      formatNumberFunction={(value) =>
        ` (${value > 0 ? '+' : ''}${numberToPercentage(value)}) `
      }
    />
  </span>
);

export const UserPerformance = forwardRef<HTMLDivElement, UserPerformanceProps>(
  (
    {
      color,
      data = { day: [], week: [], month: [], season: [], inception: [] },
      payload,
      token,
      ...props
    },
    ref
  ) => {
    const [timeRage, setTimeRage] = useState<TimeframeType>(TimeframeType.DAY);
    const parentRef = useRef(null);

    return (
      <Box ref={parentRef} {...props} className="back-test-anlysis">
        <div style={{ position: 'relative' }}>
          <ButtonGroups
            input={TimeframeInput}
            onSelect={(value: string) => setTimeRage(value as TimeframeType)}
          />
        </div>
        <div className="headline-4 medium">My Investments</div>
        <div className="headline-5" style={{ marginBottom: '33px' }}>
          Net Value Change:
          <NetValueChanged
            share_price_difference={
              data[timeRage][data[timeRage].length - 1]
                .share_price_difference as number
            }
            percentage={
              data[timeRage][data[timeRage].length - 1].percentage as number
            }
          />
          <span className="timeframe-text">
            {getTimeIntervalText(timeRage, data[timeRage])}
          </span>
        </div>
        <div className="graph-title">
          <div className="subtitle-2">Change(%)</div>
        </div>

        <LineGraph
          color={color}
          data={data[timeRage]}
          referenceAmount={0}
          type="full"
          payload={payload}
          token={token}
          lineCount={1}
          tooltipInfo={tooltipInfo(timeRage)}
        />
      </Box>
    );
  }
);

export const UserPerformanceMini = forwardRef<
  HTMLDivElement,
  UserPerformanceProps
>(
  (
    {
      color,
      data = { day: [], week: [], month: [], season: [], inception: [] },
      payload,
      token,
      ...props
    },
    ref
  ) => {
    const [timeRage, setTimeRage] = useState<TimeframeType>(TimeframeType.DAY);
    const parentRef = useRef(null);
    const isMobile = useMediaQuery('(max-width:1199px)');

    return (
      <div ref={parentRef} {...props} className="return-chart">
        {isMobile && (
          <>
            <div className="headline-5 medium timeframe-text">
              My Investments
            </div>
            <div className="grid3-mini">
              <div className="card-field-name" style={{ marginBottom: '8px' }}>
                Net Value Change:
              </div>
              <div className="medium timeframe-text">
                <NetValueChanged
                  share_price_difference={
                    data[timeRage][data[timeRage].length - 1]
                      .share_price_difference
                  }
                  percentage={
                    data[timeRage][data[timeRage].length - 1].percentage
                  }
                />
                {getTimeIntervalText(timeRage, data[timeRage])}
              </div>
            </div>
          </>
        )}
        <div className="line-graph-container">
          <ButtonGroups
            input={TimeframeInput}
            onSelect={(value: string) => setTimeRage(value as TimeframeType)}
            type="small"
          />
          <LineGraph
            color={color}
            data={data[timeRage]}
            type="mini"
            legend={false}
            payload={payload}
            token={token}
            fill={true}
            lineCount={1}
            tooltipInfo={tooltipInfo(timeRage)}
          />
        </div>
      </div>
    );
  }
);
