import { Fragment, useCallback } from 'react';
import { ProtocolInfo } from '../../../../../../constants/protocolConfig';
import {
  Strategy,
  StrategyInfo,
} from '../../../../../../constants/strategy/index';
import { StrategyType } from '../../../../../../constants/crosschain';
import { TokenInfo } from '../../../../../../constants/tokenConfig';
import {
  calculateThreshold,
  getVaultName,
  numberParseLarge,
  numberToPercentage,
} from '../../../../../../helpers/utilities';
import { QuestionMarkTooltip } from '../../../../Modals/AssetModal/toolTip';
import { NumberCounter } from '../../../NumberCounter';
import './info.scss';

// Aperture uikit
import { Tag } from '@aperture/uikit';

interface ICardProps {
  walletConnected: boolean;
  cardInfo: StrategyInfo | null;
  holdings: number;
  profits: string[];
  pageType: 'Portfolio' | 'Details' | 'DeltaNeutral';
  tvlGteCapacity: boolean;
  apy_7days: number | null;
  inceptionDate: string;
  strategy: Strategy;
}

export const CardInfo = (props: ICardProps) => {
  const {
    cardInfo: data,
    walletConnected: connected,
    pageType: page,
    holdings,
    profits = ['', '$--'],
    apy_7days,
    inceptionDate,
    strategy,
    tvlGteCapacity,
  } = props;

  const getFarmingPoolInfo = (
    tokens: TokenInfo[],
    protocols: ProtocolInfo[]
  ) => {
    const tokenString = getVaultName(tokens);
    if (protocols.length === 2)
      return (
        <>
          {`${protocols[0].name} ${tokenString}`}
          <br />
          {`${protocols[1].name} ${tokenString}`}
        </>
      );
    else return <>{`${protocols[0].name} ${tokenString}`}</>;
  };

  const getClass = (name: string, apy: number | null) => {
    if (name === 'APY') {
      return apy !== null ? (apy >= 0 ? ' positive' : ' negative') : '';
    }
    if (name === 'PROFITS') return profits[0];
    return '';
  };

  const FormatTVL = useCallback(
    () => (
      <>
        {data?.TVL !== null && data?.TVL !== undefined ? (
          <NumberCounter
            value={data.TVL}
            formatNumberFunction={(value) => `$${numberParseLarge(value, 2)}`}
          />
        ) : (
          '-'
        )}
        {' / '}
        {data?.capacity !== null && data?.capacity !== undefined ? (
          <NumberCounter
            value={Number(data?.capacity)}
            formatNumberFunction={(value) => numberParseLarge(value, 2)}
          />
        ) : (
          '-'
        )}
      </>
    ),
    [data]
  );

  const FormatAPY = () => {
    return (
      <>
        {apy_7days !== null ? (
          <NumberCounter
            value={apy_7days}
            formatNumberFunction={(value) =>
              `${numberToPercentage(Number(value))}`
            }
          />
        ) : (
          '-'
        )}
      </>
    );
  };

  const FormatHoldings = () => {
    return (
      <>
        {connected && !Number.isNaN(holdings) ? (
          <NumberCounter
            value={holdings}
            formatNumberFunction={(value) => `$${numberParseLarge(value, 2)}`}
          />
        ) : (
          '-'
        )}
      </>
    );
  };

  const FormatCurrentValue = () => {
    return (
      <>
        {!Number.isNaN(holdings) ? (
          <NumberCounter
            value={holdings}
            formatNumberFunction={(value) => `$${numberParseLarge(value, 2)}`}
          />
        ) : (
          '-'
        )}
      </>
    );
  };

  const renderStrategy = (strategyType: StrategyType) => {
    switch (strategyType) {
      case StrategyType.PSEUDO_DELTA_NEUTRAL:
        return <Tag variant={'crab'} />;
      case StrategyType.PSEUDO_1X_LONG:
        return <Tag variant={'bull'} />;
    }
  }

  const FormatStrategy = () => {
    return (
      <>
        {strategy.strategyType ? (
          renderStrategy(strategy.strategyType)
        ) : (
          '-'
        )}
      </>
    );
  };

  const FormatLeverage = useCallback(
    () => (
      <>
        {data?.leverage ? (
          <NumberCounter
            value={data.leverage}
            formatNumberFunction={(value) => `${value}X`}
          />
        ) : (
          '-'
        )}
      </>
    ),
    [data]
  );

  const FormatThreshold = useCallback(() => {
    const threshold = data
      ? calculateThreshold(data.priceMovementThreshold, data.leverage)
      : `-`;

    return (
      <NumberCounter
        value={threshold}
        formatNumberFunction={(value) =>
          typeof value === 'number'
            ? `${numberToPercentage(Number(value))}`
            : value
        }
      />
    );
  }, [data]);

  const infoProperty = {
    TVL: FormatTVL(),
    APY: FormatAPY(),
    HOLDINGS: FormatHoldings(),
    PROFITS: profits[1],
    CURRENTVALUE: FormatCurrentValue(),
    STRATEGY: FormatStrategy(),
    LEVERAGE: FormatLeverage(),
    INCEPTIONDATE: inceptionDate === '' ? '--' : inceptionDate,
    THRESHOLD: FormatThreshold(),
    FARMINGPOOL: getFarmingPoolInfo(strategy.tokens, strategy.protocols),
  };

  interface ICardInfo {
    name: keyof typeof infoProperty;
    title: string;
  }

  const dnProperty: ICardInfo[] = [
    { name: 'STRATEGY', title: 'Strategy' },
    { name: 'TVL', title: 'TVL/Capacity' },
    { name: 'APY', title: 'APY' },
  ];

  const portfolioProperty: ICardInfo[] = [
    { name: 'STRATEGY', title: 'Strategy' },
    { name: 'TVL', title: 'TVL/Capacity' },
    { name: 'HOLDINGS', title: 'My Holdings' },
    { name: 'PROFITS', title: 'Profits' },
  ];
  const investProperty: ICardInfo[] = [
    { name: 'HOLDINGS', title: 'My Holdings' },
    { name: 'PROFITS', title: 'Profits' },
  ];
  const detailProperty: ICardInfo[] = [
    { name: 'TVL', title: 'TVL/Capacity' },
    { name: 'APY', title: 'APY' },
    { name: 'LEVERAGE', title: 'Target Leverage' },
    { name: 'INCEPTIONDATE', title: 'Vault Inception Date' },
    { name: 'THRESHOLD', title: 'Price Movement Rebalance Threshold' },
    { name: 'FARMINGPOOL', title: 'Underlying Farming Pool' },
  ];

  let displayData = (dataInfo: ICardInfo[]) =>
    dataInfo.map((item, i) => {
      return (
        <>
          <div key={i} className="info-row">
            <div className="fieldName">
              <p className="subtitle-3">{item.title}</p>
              {item.name === 'TVL' &&
                (tvlGteCapacity ? <Tag variant={'full'} /> : <Tag variant={'available'} />)}
              {item.name === 'APY' && (
                <span style={{ marginLeft: '4px' }}>
                  <QuestionMarkTooltip
                    title={`This projected APY is based on past 7-day returns, excluding any occasional rebalancing costs.`}
                    placementPC={'bottom'}
                    placementMobile={'bottom'}
                  />
                </span>
              )}
            </div>
            <div className={`fieldValue`}>
              <p className={`subtitle-1${getClass(item.name, apy_7days)}`}>
                {infoProperty[item.name]}
              </p>
            </div>
          </div>
          {page === 'Details' && item.name === 'PROFITS' && <hr></hr>}
        </>
      );
    });

  return (
    <Fragment>
      {page === 'DeltaNeutral' && displayData(dnProperty)}
      {page === 'Portfolio' && displayData(portfolioProperty)}
      {page === 'Details' &&
        (connected && !Number.isNaN(holdings) && holdings > 0
          ? displayData(investProperty.concat(detailProperty))
          : displayData(detailProperty))}
    </Fragment>
  );
};
