import React, { Fragment, useMemo } from 'react';
import {
  Strategy,
  StrategyInfo,
} from '../../../../../constants/strategy/index';
import { StrategyType } from '../../../../../constants/crosschain';
import {
  useFetchAssetPrice,
  useFetchHomoraPerformace,
} from '../../../../../features/graph/useFetchGraphData';
import {
  calculate_timeframe_apy,
  getReturnData,
  TimeframeType,
} from '../../../../../helpers/graph';
import {
  getVaultName,
  timestampToUTCString,
} from '../../../../../helpers/utilities';
import { useStore } from '../../../../../store';
import { useOpenModal } from '../../../../../store/base/application/hooks';
import { ModalType } from '../../../../../store/base/AppSlice';
import { PositionInfo } from '../../../../../types';
import { MobilePopOver } from '../../../Modal/MobilePopOver';
import { Button } from '../../Button';
import { VaultItem } from '../VaultItem';
import './card.scss';
import { CardInfo } from './CardInfo';
import { StrategyTabs } from './StrategyTabs';

interface ICardProps {
  strategyInfo: StrategyInfo | null;
  strategy: Strategy;
  positionInfo: PositionInfo | null;
  pageType: 'Portfolio' | 'Details' | 'DeltaNeutral';
  walletConnected: boolean;
  profits: string[];
  tvlGteCapacity: boolean;
}

export const StrategyCard = (props: ICardProps) => {
  const {
    strategyInfo: data,
    strategy,
    positionInfo,
    walletConnected,
    pageType,
    profits,
    tvlGteCapacity,
  } = props;
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  const vaultTitle = getVaultName(strategy.tokens);

  const { data: assetPrice } = useFetchAssetPrice();
  const { data: homoraPerformance } = useFetchHomoraPerformace();
  const returns = useMemo(
    () =>
      getReturnData(
        assetPrice,
        homoraPerformance,
        strategy.strategyId
      ),
    [assetPrice, homoraPerformance, strategy.strategyId]
  );
  const returnData = returns.inception;

  const vaultItem = (isCard = true) => {
    return (
      <VaultItem
        {...{
          title: isCard ? `${vaultTitle}(${data?.leverage}X)` : vaultTitle,
          tokens: strategy.tokens,
          protocols: strategy.protocols,
          strategyType: strategy.strategyType,
        }}
      />
    );
  };

  const handleInvest = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    e.stopPropagation();
    openInvestModal();
  };

  const handleWithdraw = (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ) => {
    e.stopPropagation();
    openWithdrawlModal();
  };

  const modalTitle = (strategyType: StrategyType) => {
    switch (strategyType) {
      case StrategyType.PSEUDO_DELTA_NEUTRAL:
        return 'Crab Market Leveraged Farming';
      case StrategyType.PSEUDO_1X_LONG:
        return 'Bull Market Leveraged Farming';
    }
  }
  const modalProps = {
    strategyInfo: props.strategyInfo,
    positionInfo: props.positionInfo,
    vaultItem: vaultItem(false),
    title: modalTitle(props.strategy?.strategyType),
  };

  const openInvestModal = useOpenModal(ModalType.INVEST, {
    invest: true,
    ...modalProps,
  });

  const openWithdrawlModal = useOpenModal(ModalType.INVEST, {
    invest: false,
    ...modalProps,
  });

  return (
    <Fragment>
      <div onClick={handleOpen} className="cardContainer">
        <div>{vaultItem()}</div>
        <CardInfo
          walletConnected={walletConnected}
          cardInfo={props.strategyInfo}
          holdings={Number(positionInfo?.equity)}
          profits={profits}
          pageType={pageType}
          strategy={strategy}
          apy_7days={calculate_timeframe_apy(returns.week, TimeframeType.WEEK)}
          inceptionDate={
            returnData && returnData.length > 0
              ? timestampToUTCString(
                returnData[0]!.timestamp_sec,
                'DayMonthYear'
              )
              : ''
          }
          tvlGteCapacity={tvlGteCapacity}
        />
        <div className="itemRow" style={{ justifyContent: 'space-between' }}>
          <Button
            variant="outline"
            size="small"
            text={'Deposit'}
            onClick={(event) => handleInvest(event)}
            disabled={tvlGteCapacity || !walletConnected}
            style={{ width: '40%', padding: '3px' }}
          />
          <Button
            variant="info"
            size="small"
            text={'Withdraw'}
            onClick={(event) => handleWithdraw(event)}
            disabled={
              !(
                walletConnected &&
                positionInfo &&
                positionInfo.equity &&
                Number(positionInfo.equity) > 0
              )
            }
            style={{ width: '40%', padding: '3px' }}
          />
        </div>
      </div>
      <MobilePopOver
        open={open}
        onClose={handleClose}
        className="strategy-pop-over"
        sx={{ padding: 0 }}
      >
        <StrategyTabs
          clickDeposit={(event) => handleInvest(event)}
          clickWithdraw={(event) => handleWithdraw(event)}
          strategy={strategy}
          strategyInfo={props.strategyInfo}
          holdings={Number(positionInfo?.equity)}
          profits={profits}
          walletConnected={walletConnected}
          pageType={pageType}
          positionInfo={positionInfo}
          tvlGteCapacity={tvlGteCapacity}
        />
      </MobilePopOver>
    </Fragment>
  );
};
