import { forwardRef, useEffect, useRef, useState } from 'react';
import { Box, NumberCounter } from '../../../../';
import { calculate_backtest_analysis_apy } from '../../../../../../helpers/graph';
import {
  numberToPercentage,
  timestampToUTCString,
} from '../../../../../../helpers/utilities';
import { LineGraph } from '../../../../Charts/LineGraph';
import './index.scss';
// Zustand Store
import { IStore, useStore } from '../../../../../../store';

interface BackTestAnlysisProps {
  color: any;
  data: any;
  referenceAmount: number;
  title: string;
}

interface dataInfo {
  name: string;
  title: string;
  value: number | null;
}

export const BackTestAnlysis = forwardRef<HTMLDivElement, BackTestAnlysisProps>(
  (
    {
      color,
      data = {},
      referenceAmount = 0,
      title = 'Backtest Result',
      ...props
    },
    ref
  ) => {
    const [width, setWidth] = useState(0);
    const parentRef = useRef(null);
    const darkMode = useStore((state: IStore) => state.themeSlice.darkMode);

    useEffect(() => {
      parentRef.current && setWidth(parentRef.current!.offsetWidth);
      // console.log(parentRef.current!.offsetWidth);
    }, [parentRef]);

    const percentage =
      data && data.length > 0
        ? {
          return: data[data.length - 1].percentage,
          apy: calculate_backtest_analysis_apy(data),
        }
        : {
          return: null,
          apy: null,
        };

    const backtestData: dataInfo[] = [
      { name: 'Return', title: 'Backtest Return:', value: percentage.return },
      { name: 'APY', title: 'Annualized Return:', value: percentage.apy },
    ];

    let displayData = (dataInfo: dataInfo[]) =>
      dataInfo.map((item, i) => {
        return (
          <>
            {item.title}
            <span
              className={`span-1${item.value !== null
                  ? item.value >= 0
                    ? ' positive'
                    : ' negative'
                  : ''
                }`}
            >
              {item.value !== null ? (
                <NumberCounter
                  value={item.value}
                  formatNumberFunction={(value) => numberToPercentage(value)}
                />
              ) : (
                '-'
              )}
            </span>
          </>
        );
      });

    return (
      <Box ref={parentRef} {...props} className="back-test-anlysis">
        <div className="headline-4 semiBold">Backtest Analysis</div>
        <div className="headline-5" style={{ marginBottom: '33px' }}>
          {displayData(backtestData)}
        </div>
        <div className="graph-title">
          <div className="subtitle-2">Backtest Result ({title})</div>
          <div className="headline-5 float-right">
            {data && data.length > 0
              ? `${timestampToUTCString(
                data[0].timestamp_sec,
                'MonthYear'
              )} - ${timestampToUTCString(
                data[data.length - 1].timestamp_sec,
                'MonthYear'
              )}`
              : ''}
          </div>
        </div>
        <LineGraph
          color={color}
          data={data}
          referenceAmount={referenceAmount}
          type="full"
          lineCount={1}
          tooltipInfo={{
            day: false,
            hours: true,
            value: [
              {
                color: darkMode ? '#19EE94' : '#7E51FD',
                text: 'Return',
                value: 'percentage',
                suffix: '%',
                colorChanging: true,
              },
            ],
          }}
        />
      </Box>
    );
  }
);
