import Grid from '@mui/material/Grid';
import { forwardRef, useRef, useState } from 'react';
import {
  calculate_timeframe_apy,
  getTimeIntervalText,
  TimeframeInput,
  TimeframeType,
} from '../../../../../../helpers/graph';
import { numberToPercentage } from '../../../../../../helpers/utilities';
import { LineGraph } from '../../../../Charts/LineGraph';
import { ButtonGroups } from '../../../Button/ButtonGroups';
import './index.scss';

interface ReturnMiniProps {
  color: any;
  data: any;
  payload: any;
  token: any;
  details: boolean;
  lineCount?: 1 | 2;
  fill?: boolean;
}

export const ReturnMini = forwardRef<HTMLDivElement, ReturnMiniProps>(
  (
    {
      details = false,
      color,
      data = { day: [], week: [], month: [], season: [], inception: [] },
      payload,
      token,
      lineCount = 1,
      fill = false,
      ...props
    },
    ref
  ) => {
    const [timeRage, setTimeRage] = useState<TimeframeType>(TimeframeType.DAY);
    const [APY, setAPY] = useState<number | null>(
      calculate_timeframe_apy(data.day, TimeframeType.DAY)
    );
    const parentRef = useRef(null);

    return (
      <div ref={parentRef} {...props} className="return-chart">
        {details && (
          <>
            <div className="headline-5 medium timeframe-text">
              Return Chart
            </div>
            <Grid
              container
              className="grid3-mini"
              justifyContent={'space-evenly'}
            >
              <Grid item>
                <div className="card-field-name" style={{ marginBottom: '8px' }}>
                  Return:
                </div>
                <div className="medium">
                  <span
                    className={`${data[timeRage][data[timeRage].length - 1].share_price -
                      data[timeRage][0].share_price >=
                      0
                      ? ' positive'
                      : ' negative'
                      }`}
                  >
                    {`${numberToPercentage(
                      (data[timeRage][data[timeRage].length - 1].share_price -
                        data[timeRage][0].share_price) /
                      data[timeRage][0].share_price
                    )} `}
                  </span>
                  {getTimeIntervalText(timeRage, data[timeRage])}
                </div>
              </Grid>
              <Grid item>
                <div className="card-field-name" style={{ marginBottom: '8px' }}>
                  APY:
                </div>
                <div
                  className={`medium${APY === null ? '' : APY >= 0 ? ' positive' : ' negative'
                    }`}
                >
                  {APY !== null ? `${numberToPercentage(APY)}` : '-'}
                </div>
              </Grid>
            </Grid>
          </>
        )}
        <div className="line-graph-container">
          <ButtonGroups
            input={TimeframeInput}
            onSelect={(value: string) => {
              setTimeRage(value as TimeframeType);
              setAPY(
                calculate_timeframe_apy(data[value], value as TimeframeType)
              );
            }}
            type="small"
          />
          <LineGraph
            color={color}
            data={data[timeRage]}
            type="mini"
            legend={details}
            payload={payload}
            token={token}
            fill={fill}
            lineCount={lineCount}
            tooltipInfo={{
              day: timeRage === TimeframeType.WEEK,
              hours: timeRage === TimeframeType.DAY,
              value: [
                {
                  color: '#7BEFFF',
                  text: 'Return',
                  value: 'percentage',
                  suffix: '%',
                  colorChanging: true,
                },
                {
                  color: '#19EE94',
                  text: 'AVAX Price',
                  value: 'price',
                  prefix: '$',
                },
              ],
            }}
          />
        </div>
      </div>
    );
  }
);
