export function useFetchBacktestAnalysis() {
  const param_performance = {
    TableName: 'homora_performance_backtest_v2',
    Select: 'ALL_ATTRIBUTES',
  };
  return useQuery('fetchBacktestAnalysis', () =>
    documentClient
      .scan(param_performance)
      .promise()
      .then(async (res) => {
        const result = await res?.Items?.reduce((r: any, a: any) => {
          r[a.vault_id] = r[a.vault_id] || [];
          r[a.vault_id].push(a);
          return r;
        }, Object.create(null));
        Object.keys(result).forEach((key) => {
          const initial_price = result[key][0].value;
          result[key] = result[key].map((itm: any) => ({
            timestamp_sec: itm.timestamp_sec,
            percentage: (itm.value - initial_price) / initial_price,
          }));
        });
        return result;
      })
  );
}
export function useFetchUserGraphInfo(positionId: String) {
  const chainId = '6'; // make this a arguments when we suppor multichains
  const param_user_performance = {
    TableName: 'user_portfolio_tracking',
    KeyConditionExpression: 'chain_id_position_id = :a',
    ExpressionAttributeValues: {
      ':a': `${chainId}-${positionId}`,
    },
  };
  return useQuery(`fetchUserGraphInfo${positionId}`, () =>
    documentClient
      .query(param_user_performance)
      .promise()
      .then(async (res) => {
        return res.Items;
      })
  );
}

export function useFetchPositionPNL(chainId: number, positionId: string) {
  const partition_key = `${chainId}-${positionId}`;
  const param_position_pnl = {
    TableName: 'position_pnl',
    KeyConditionExpression: 'chain_position = :a',
    ExpressionAttributeValues: {
      ':a': `${partition_key}`,
    },
    // ScanIndexForward: false,
  };
  return useQuery(`useFetchPositionPNL${partition_key}`, () =>
    documentClient
      .query(param_position_pnl)
      .promise()
      .then(async (res) => {
        return res.Items;
      })
  );
}
