import { forwardRef, useRef, useState } from 'react';
import { Box, ButtonGroups, NumberCounter } from '../../../../';
import {
  calculate_timeframe_apy,
  getTimeIntervalText,
  TimeframeInput,
  TimeframeType,
} from '../../../../../../helpers/graph';
import { numberToPercentage } from '../../../../../../helpers/utilities';
import { LineGraph } from '../../../../Charts/LineGraph';
// Zustand Store
import { IStore, useStore } from '../../../../../../store';

interface ReturnProps {
  color: any;
  data: any;
  payload: any;
  token: any;
}

export const Return = forwardRef<HTMLDivElement, ReturnProps>(
  (
    {
      color,
      data = { day: [], week: [], month: [], season: [], inception: [] },
      payload,
      token,
      ...props
    },
    ref
  ) => {
    const [timeRage, setTimeRage] = useState<TimeframeType>(TimeframeType.DAY);
    const [APY, setAPY] = useState<number | null>(
      calculate_timeframe_apy(data.day, TimeframeType.DAY)
    );
    const parentRef = useRef(null);
    const darkMode = useStore((state: IStore) => state.themeSlice.darkMode);

    return (
      <Box ref={parentRef} {...props} className="back-test-anlysis">
        <div style={{ position: 'relative' }}>
          <ButtonGroups
            input={TimeframeInput}
            onSelect={(value: string) => {
              setTimeRage(value as TimeframeType);
              setAPY(
                calculate_timeframe_apy(data[value], value as TimeframeType)
              );
            }}
          />
        </div>
        <div style={{ marginBottom: '32px' }}>
          <div className="headline-4 medium">Vault Return</div>
          <div className="headline-5" style={{ marginBottom: '10px' }}>
            Return:
            <span
              className={`span-1${
                data?.[timeRage]?.[data[timeRage]?.length - 1]?.share_price ===
                undefined
                  ? ''
                  : data[timeRage][data[timeRage].length - 1]?.share_price >=
                    data[timeRage][0].share_price
                  ? ' positive'
                  : ' negative'
              }`}
            >
              {data?.[timeRage]?.[data[timeRage]?.length - 1]?.share_price ? (
                <NumberCounter
                  value={
                    (data?.[timeRage]?.[data[timeRage]?.length - 1]
                      ?.share_price -
                      data[timeRage][0].share_price) /
                    data[timeRage][0].share_price
                  }
                  formatNumberFunction={(value) => numberToPercentage(value)}
                />
              ) : (
                '-'
              )}
            </span>
            <span className="timeframe-text">
              {getTimeIntervalText(timeRage, data[timeRage])}
            </span>
          </div>
          <div className="headline-5">
            APY Based on {getTimeIntervalText(timeRage, data[timeRage])} Return:
            <span
              className={`span-1${APY === null ? '' : APY >= 0 ? ' positive' : ' negative'
                }`}
            >
              {APY !== null ? (
                <NumberCounter
                  value={APY}
                  formatNumberFunction={(value) => numberToPercentage(value)}
                />
              ) : (
                '-'
              )}
            </span>
          </div>
        </div>
        <div className="graph-title">
          <div className="subtitle-2">Return(%)</div>
          <div className="subtitle-2 float-right">{token} Price($)</div>
        </div>
        <LineGraph
          color={color}
          data={data[timeRage]}
          referenceAmount={0}
          type="full"
          legend
          payload={payload}
          token={token}
          lineCount={2}
          tooltipInfo={{
            day: timeRage === TimeframeType.WEEK,
            hours: true,
            value: [
              {
                color: darkMode ? '#7BEFFF' : '#7E51FD',
                text: 'Return',
                value: 'percentage',
                suffix: '%',
                colorChanging: true,
              },
              {
                color: darkMode ? '#19EE94' : '#1A9A64',
                text: 'AVAX Price',
                value: 'price',
                prefix: '$',
              },
            ],
          }}
        />
      </Box>
    );
  }
);
