import './index.scss';
import styled from 'styled-components';
import { useMediaQuery } from '@mui/material'

export interface TooltipValue {
  color: String;
  colorChanging?: boolean;
  text: String;
  addSignForValue?: boolean;
  value: String; // attributes name
  changingRate?: String; // attributes name
  prefix?: String;
  suffix?: String;
}

export interface TooltipMiniProps {
  active: any;
  payload: any[];
  value: TooltipValue[];
}

export interface TooltipProps {
  active: any;
  payload: any[];
  day?: boolean | undefined;
  hours?: boolean | undefined;
  value: TooltipValue[];
}

const Dot = styled.div`
  box-sizing: border-box;
  width: 10px;
  height: 10px;
  border: 2px solid ${props => props.color};
  border-radius: 50%;
  display: inline-block;
  margin-right: 8px;
`

const weekday = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"]

export const Tooltip = ({
  active,
  payload,
  day = false,
  hours = false,
  value,
  ...props
}: TooltipProps) => {
  const mobile = useMediaQuery('(max-width:1199px)');
  if (active && payload && payload.length) {
    const data = payload[0].payload
    const date = new Date(data.timestamp_sec * 1000)

    return (
      <div className={['graph-tooltip', mobile ? ' mobile' : ''].join(' ')}>
        <div className='tooltip-float-text'>
          {day && weekday[date.getDay()] + ', '}
          {date.toLocaleDateString('en-us')}
          {hours && <div className={['tooltip-float-right', mobile ? ' mobile' : ''].join(' ')}>
            {date.toLocaleTimeString('en-us')}
          </div>}
        </div>
        {value.map(item => {
          const positive = data.percentage > 0
          const negative = data.percentage < 0

          const percentage = item.suffix === "%"
          const value = data[item.value] * (percentage ? 100 : 1)
          const changingRate = item.changingRate && data[item.changingRate] * 100 // 3 decimal places

          return <div className='tooltip-float-text'>
            {!mobile && <Dot color={item.color} />}
            {item.text}:
            <div
              className={['tooltip-float-right', mobile ? ' mobile' : ''].join(' ')}
              style={{ color: item.colorChanging && positive ? '#11ffa9' : item.colorChanging && negative ? '#ff9a9a' : 'white' }}
            >
              {value < 0 ? "-" : (percentage || item.addSignForValue) && value > 0 ? "+" : ""}{item.prefix}{Math.abs(value).toFixed(percentage ? 3 : 2)}{item.suffix}
              {changingRate && ` (${changingRate > 0 ? "+" : changingRate < 0 ? "-" : ""}${Math.abs(changingRate).toFixed(3)}%)`}
            </div>
          </div>
        })}
      </div>
    );
  }
  return null;
};

export const TooltipMini = ({
  active,
  payload,
  value,
  ...props
}: TooltipMiniProps) => {
  if (active && payload && payload.length) {
    const data = payload[0].payload
    const date = new Date(data.timestamp_sec * 1000)

    const percentage = value[0].suffix === "%"
    const amount = data[value[0].value] * (percentage ? 100 : 1)

    return (
      <div className={'graph-tooltip mini'}>
        <div className='relative'>
          <span style={{ color: data.percentage > 0 ? '#11ffa9' : data.percentage < 0 ? '#ff9a9a' : 'white' }}>
            {amount < 0 ? "-" : percentage && amount > 0 ? "+" : ""}{value[0].prefix}{Math.abs(amount).toFixed(percentage ? 3 : 2)}{value[0].suffix}
          </span>
          <div className={'tooltip-float-right'} >
            {date.toLocaleDateString('en-us')}
          </div>
        </div>
      </div>
    );
  }
  return null;
};
