import { Grid } from '@mui/material';
import { useMemo } from 'react';
import { ProtocolInfo } from '../../../../../../constants/protocolConfig';
import {
  Strategy,
  StrategyInfo,
} from '../../../../../../constants/strategy/index';
import {
  useFetchAssetPrice,
  useFetchBacktestAnalysis,
  useFetchHomoraPerformace,
} from '../../../../../../features/graph/useFetchGraphData';
import { getReturnData } from '../../../../../../helpers/graph';
import { calculateThreshold } from '../../../../../../helpers/utilities';
import { PositionInfo } from '../../../../../../types';
import { DetailsDropdown } from '../../../DetailsDropdown';
import {
  BacktestAnalysis,
  Info,
  Return,
  UserPerformance,
} from '../DropdownInfo/PDN';
import { PdnStrategyDetails } from '../PdnStrategyDetails';

interface PdnDropdownProps {
  protocols?: ProtocolInfo[];
  vaultTitle?: string;
  portfolio: boolean;
  strategyInfo: StrategyInfo;
  strategy: Strategy;
  positionInfo: PositionInfo | null;
}

export const PdnDropdown = ({
  portfolio,
  protocols = [],
  vaultTitle = '',
  strategyInfo,
  strategy,
  positionInfo,
}: PdnDropdownProps) => {
  const mockProps = {
    threshold: calculateThreshold(
      strategyInfo.priceMovementThreshold,
      strategyInfo.leverage
    ),
    leverage: strategyInfo.leverage,
    modalChildren: (
      <PdnStrategyDetails
        positionInfo={positionInfo ?? undefined}
        strategyInfo={strategyInfo}
        vaultTitle={vaultTitle}
      />
    ),
  };
  const { data: backtestAnalysisData } = useFetchBacktestAnalysis();
  const { data: assetPrice } = useFetchAssetPrice();
  const { data: homoraPerformance } = useFetchHomoraPerformace();
  const returns = useMemo(
    () =>
      getReturnData(
        assetPrice,
        homoraPerformance,
        strategy.strategyId
      ),
    [assetPrice, homoraPerformance, strategy.strategyId]
  );

  return (
    <DetailsDropdown>
      <Grid container>
        <Grid item xl={2.9} lg={2.9} md={2.9}>
          <Info {...{ ...mockProps, protocols, vaultTitle }} />
        </Grid>
        {portfolio && (
          <Grid item xl={4.55} lg={4.55} md={4.55} sx={{ paddingLeft: '2px' }}>
            <UserPerformance
              {...{
                ...mockProps,
                strategyInfo,
                positionInfo: positionInfo ?? undefined,
                vaultTitle,
              }}
            />
          </Grid>
        )}
        <Grid item xl={4.55} lg={4.55} md={4.55} sx={{ paddingLeft: '2px' }}>
          <Return
            {...{
              ...mockProps,
              strategyInfo,
              positionInfo: positionInfo ?? undefined,
              vaultTitle,
              returns,
            }}
          />
        </Grid>
        {!portfolio && (
          <Grid item xl={4.55} lg={4.55} md={4.55} sx={{ paddingLeft: '2px' }}>
            <BacktestAnalysis
              {...{
                ...mockProps,
                positionInfo: positionInfo ?? undefined,
                strategyInfo,
                vaultTitle,
                backtestAnalysisData,
              }}
            />
          </Grid>
        )}
      </Grid>
    </DetailsDropdown>
  );
};
