import { DropdownInfoBox } from '../../../';
import { Box, Modal, ReturnMini } from '../../../../../..';
import { StrategyInfo } from '../../../../../../../../constants/strategy';
import { PositionInfo } from '../../../../../../../../types';
import { PdnStrategyDetails } from '../../../PdnStrategyDetails';

// Zustand Store
import { IStore, useStore } from '../../../../../../../../store';

export interface ReturnProps {
  modalChildren: any;
  positionInfo?: PositionInfo;
  strategyInfo: StrategyInfo;
  vaultTitle: any;
  returns: any;
}
export const Return = ({
  modalChildren,
  strategyInfo,
  vaultTitle,
  positionInfo,
  returns,
}: ReturnProps) => {
  const returnData = returns.inception;
  const token =
    returnData && returnData.length > 0
      ? returnData[returnData.length - 1].ticker
      : 'Token';
  const darkMode = useStore((state: IStore) => state.themeSlice.darkMode);

  return (
    <>
      <DropdownInfoBox
        title={'Return'}
        viewDetails={{
          buttonText: 'View Details',
          modalChildren,
        }}
        first
      >
        <Modal
          modalClassName="modal-pdn-strategy-details"
          trigger={
            <Box
              className="line-graph-box"
              style={{ margin: '12px 16px 16px', padding: '0px' }}
            >
              <ReturnMini
                details={false}
                color={{ percentage: darkMode ? '#7BEFFF' : '#7E51FD', price: '#19EE94' }}
                data={returns}
                token={token}
                payload={[
                  {
                    value: 'Return',
                    type: 'line',
                    id: 'percentage',
                    color: '#7BEFFF',
                  },
                  {
                    value: `${token} Price`,
                    type: 'line',
                    id: 'price',
                    color: '#19EE94',
                  },
                ]}
                fill={true}
              />
            </Box>
          }
        >
          <PdnStrategyDetails
            strategyInfo={strategyInfo}
            positionInfo={positionInfo}
            vaultTitle={vaultTitle}
            scrollToReturn
          />
        </Modal>
      </DropdownInfoBox>
    </>
  );
};
