import Grid from '@mui/material/Grid';
import React, { Fragment, useMemo } from 'react';
import { useStore } from '../../store';
import { Box } from '../v1';
import './styles/Overview.scss';

import UpdateIcon from '@mui/icons-material/Update';
import { useMediaQuery } from '@mui/material';
import { UseQueryResult } from 'react-query';
import { Link } from 'react-router-dom';
import {
  useFetchEvmPositionInfo,
  useFetchEvmPositions,
} from '../../features/positions/hooks';
import {
  BN,
  calculateProfits,
  numberParseLarge,
  profitsClassName,
  profitsDataParser,
} from '../../helpers/utilities';
import textContext from '../../textContext';
import { IPNLDetails, PositionInfo } from '../../types';

type OverviewProps = {
  activeSubpage: 'PDN' | 'LO';
};

type OverviewData = {
  equity: string;
  profits: IPNLDetails | undefined;
};

// component for the responsive top portion of page
export default function Overview(props: OverviewProps) {
  const language = useStore((state) => state.appSlice.language);
  const isWalletConnected = useStore(
    (state) => state.chainSlice.isWalletConnected
  );
  const text = textContext[language as keyof object]['portfolio']['overview'];
  const [subpage, setSubpage] = React.useState<'PDN' | 'LO'>(
    props.activeSubpage
  );
  const isPC = useMediaQuery('(min-width:1200px)');

  const handleActivePage = (name: 'PDN' | 'LO') => {
    setSubpage(name);
  };
  // TODO: Is it necessary to fetch all positions again just to calculate the `Total Value`?
  //  It has been done in `PositionContainer`. Can we do it in `Portfolio` and pass them in `props`?
  const { data: positionInfos } = useFetchEvmPositionInfo();
  const positionResults = useFetchEvmPositions(positionInfos);

  /*useEffect(() => {
    if (!positionResults || positionResults.length < 1)
      return setTotal('$0.00');
    const total = positionResults
      .map((res: UseQueryResult<any, any>) => {
        if (!res.data) return '0';

        const data = res.data as PositionInfo;
        return data.equity;
      })
      .reduce((acc: BigNumber, cur: BigNumber) => acc.plus(BN(cur)), BN(0))
      .toString();
    setTotal(`$${numberParseLarge(total, 2)}`);
  }, [positionResults]);*/

  const total_PNL_data = useMemo(() => {
    let result = {
      equity: '0',
      profits: undefined,
    } as OverviewData;
    let setProfits = true;

    positionResults
      .map((res: UseQueryResult<any, any>) => {
        if (res.data) {
          const data = res.data as PositionInfo;
          return {
            equity: data.equity,
            profits: data.profits,
          } as OverviewData;
        }
        return {
          equity: '0',
          profits: undefined,
        } as OverviewData;
      })
      .forEach((item) => {
        result.equity = BN(result.equity).plus(item.equity).toString();
        if (item.profits) {
          if (setProfits) {
            result.profits = {
              open_date: new Date().getTime(),
              net_deposit: '0',
              equity: '0',
              net_deposit_24h: '0',
              equity_24h_ago: '0',
              profits: '0',
              profits_percentage: 0,
              today_profits: '0',
              today_profits_percentage: 0,
            };
            setProfits = false;
          }
          if (result.profits) {
            if (result.profits.open_date > item.profits.open_date) {
              result.profits.open_date = item.profits.open_date;
            }
            result.profits.equity = BN(result.profits.equity)
              .plus(item.profits.equity)
              .toString();
            result.profits.equity_24h_ago = BN(result.profits.equity_24h_ago)
              .plus(item.profits.equity_24h_ago)
              .toString();
            result.profits.profits = BN(result.profits.profits)
              .plus(item.profits.profits)
              .toString();
            result.profits.net_deposit = BN(result.profits.net_deposit)
              .plus(item.profits.net_deposit)
              .toString();
            result.profits.today_profits = BN(result.profits.today_profits)
              .plus(item.profits.today_profits)
              .toString();
            result.profits.net_deposit_24h = BN(result.profits.net_deposit_24h)
              .plus(item.profits.net_deposit_24h)
              .toString();
          }
        }
      });
    return result;
  }, [positionResults]);

  const total = useMemo(
    () =>
      total_PNL_data && total_PNL_data.equity
        ? `$${numberParseLarge(total_PNL_data.equity, 2)}`
        : `$${numberParseLarge(0, 2)}`,
    [total_PNL_data]
  );

  const profitsResult = useMemo(
    () =>
      total_PNL_data.profits
        ? calculateProfits(total_PNL_data.profits)
        : undefined,
    [total_PNL_data]
  );

  const totalPL = useMemo(() => {
    const profits_Text = profitsResult
      ? profitsDataParser(total_PNL_data.equity, profitsResult)
      : '$--';

    return profitsResult
      ? [profitsClassName(profits_Text, profitsResult.profits), profits_Text]
      : ['', profits_Text];
  }, [total_PNL_data, profitsResult]);

  const todayPL = useMemo(() => {
    const profits_Text = profitsResult
      ? profitsDataParser(total_PNL_data.equity, profitsResult, true)
      : '$--';

    return profitsResult
      ? [
          profitsClassName(profits_Text, profitsResult.today_profits),
          profits_Text,
        ]
      : ['', profits_Text];
  }, [total_PNL_data, profitsResult]);

  const ViewHistory = () => (
    <Link
      to="/portfolio/history"
      style={{ position: 'absolute', top: '33px', right: '2.50%' }}
    >
      <UpdateIcon fontSize="medium" sx={{ color: 'white' }} />
      <div className={`view-history${!isPC ? ' subtitle-3' : ' button-5'}`}>
        {text.history}
      </div>
    </Link>
  );

  const SectionGroups = () => (
    <div className="section-groups subtitle-1">
      <div
        className={`section-name${subpage === 'PDN' ? ' active' : ''}`}
        onClick={() => handleActivePage('PDN')}
      >
        {!isPC ? text.section.dn : text.section.pdn}
      </div>
      <div
        className={`section-name${subpage === 'LO' ? ' active' : ''}`}
        onClick={() => handleActivePage('LO')}
      >
        {text.section.lending}
      </div>
    </div>
  );

  const ValueOverview = () => {
    const values = [
      { title: 'Total P&L', value: totalPL[1], className: totalPL[0] },
      { title: 'Today’s P&L', value: todayPL[1], className: todayPL[0] },
    ];

    return values.map((item, i) => {
      return (
        <Grid item key={i} className="item-block">
          <p className={isPC ? 'subtitle-2' : 'subtitle-3'}>{item.title}</p>
          <p
            className={`${isPC ? 'headline-3' : 'headline-5'}${item.className}`}
          >
            {item.value}
          </p>
        </Grid>
      );
    });
  };

  return (
    <Fragment>
      <Box
        style={{
          position: 'relative',
        }}
        className="overviewClass"
      >
        {/* {width > 1199 && <SectionGroups />}
        <ViewHistory /> */}
        <div className={`title ${isPC ? 'headline-2' : 'headline-3'}`}>
          {text.title}
        </div>
        <Grid container className="totalContainer">
          <Grid item className="item-block">
            <p className={isPC ? 'subtitle-2' : 'headline-5'}>Total Value</p>
            <p className={isPC ? 'headline-1' : 'headline-2'}>
              {isWalletConnected ? total : '$--'}
            </p>
          </Grid>
          {isWalletConnected && <>{ValueOverview()}</>}
        </Grid>
      </Box>
      {/* {width < 1200 && <SectionGroups />} */}
    </Fragment>
  );
}
