import React, { Fragment, useMemo, useState } from 'react';
import { Strategy, StrategyInfo } from '../../../../../../constants/strategy';
import {
  useFetchAssetPrice,
  useFetchBacktestAnalysis,
  useFetchHomoraPerformace,
  useFetchUserGraphInfo,
} from '../../../../../../features/graph/useFetchGraphData';
import {
  calculate_timeframe_apy,
  getPerformanceData,
  getReturnData,
  TimeframeType,
} from '../../../../../../helpers/graph';
import {
  getVaultName,
  timestampToUTCString,
} from '../../../../../../helpers/utilities';
import { PositionInfo } from '../../../../../../types';
import { Button } from '../../../Button';
import { BackTestAnlysisMini } from '../../LineGraph/BackTestAnlysisMini';
import { ReturnMini } from '../../LineGraph/ReturnMini';
import { UserPerformanceMini } from '../../LineGraph/UserPerformance';
import { VaultItem } from '../../VaultItem';
import { CardInfo } from '../CardInfo';
import './tab.scss';
// Zustand Store
import { IStore, useStore } from '../../../../../../store';

interface DetailsProps {
  clickDeposit: (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => void;
  clickWithdraw: (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => void;
  strategyInfo: StrategyInfo | null;
  strategy: Strategy;
  walletConnected: boolean;
  pageType: 'Portfolio' | 'Details' | 'DeltaNeutral';
  positionInfo: PositionInfo | null;
  holdings: number;
  profits: string[];
  tvlGteCapacity: boolean;
}

export const StrategyTabs = ({
  walletConnected = false,
  pageType = 'DeltaNeutral',
  positionInfo,
  strategyInfo,
  holdings,
  profits,
  clickDeposit,
  clickWithdraw,
  tvlGteCapacity,
  strategy,
  ...props
}: DetailsProps) => {
  // const { strategyInfo: data, holdings } = props;
  const [activeTab, setActiveTab] = useState('Overview');
  const darkMode = useStore((state: IStore) => state.themeSlice.darkMode);

  const { data: backtestAnalysisData } = useFetchBacktestAnalysis();
  const { data: assetPrice } = useFetchAssetPrice();
  const { data: homoraPerformance } = useFetchHomoraPerformace();
  const { data: performance } = useFetchUserGraphInfo(
    positionInfo?.positionId ?? '-1'
  );

  const performanceList = getPerformanceData(performance, positionInfo?.equity);
  const returns = useMemo(
    () =>
      getReturnData(
        assetPrice,
        homoraPerformance,
        strategy.strategyId
      ),
    [assetPrice, homoraPerformance, strategy.strategyId]
  );
  const returnData = returns.inception;
  const token =
    returnData && returnData.length > 0
      ? returnData[returnData.length - 1].ticker
      : 'Token';
  const title = useMemo(
    () =>
      strategyInfo?.tokens && strategyInfo.leverage
        ? `${getVaultName(strategyInfo.tokens)}(${strategyInfo.leverage}X)`
        : ``,
    [strategyInfo]
  );
  const vaultItem = (
    <VaultItem
      {...{
        title,
        tokens: strategy.tokens,
        protocols: strategy.protocols,
        strategyType: strategy.strategyType,
      }}
    />
  );

  const handleActiveTab = (tabName: string) => {
    setActiveTab(tabName);
  };

  const navBar = () => {
    const tabName =
      pageType === 'Portfolio'
        ? ['Overview', 'Investments']
        : ['Overview', 'Return', 'Backtest'];
    return tabName.map((tabName, i) => {
      return (
        <li
          key={i}
          className={activeTab === tabName ? 'active' : ''}
          onClick={() => handleActiveTab(tabName)}
        >
          <div className="tabName button-3">{tabName}</div>
        </li>
      );
    });
  };

  return (
    <div className="strategyContainer">
      <div className="tabNav">{navBar()}</div>
      <div>{vaultItem}</div>
      <div className="strategy-content">
        {activeTab === 'Overview' && (
          <Fragment>
            <div className="headline-5">{'Overview'}</div>
            <CardInfo
              walletConnected={walletConnected}
              cardInfo={strategyInfo}
              holdings={holdings}
              profits={profits}
              pageType="Details"
              apy_7days={
                returns?.week
                  ? calculate_timeframe_apy(returns.week, TimeframeType.WEEK)
                  : null
              }
              inceptionDate={
                returnData && returnData.length > 0
                  ? timestampToUTCString(
                      returnData[0]!.timestamp_sec,
                      'DayMonthYear'
                    )
                  : ''
              }
              tvlGteCapacity={tvlGteCapacity}
              strategy={strategy}
            />
          </Fragment>
        )}
        {activeTab === 'Return' && (
          <Fragment>
            <ReturnMini
              color={{
                percentage: darkMode ? '#7BEFFF' : '#7E51FD',
                price: darkMode ? '#19EE94' : '#008E97'
              }}
              data={returns}
              token={token}
              payload={[
                {
                  value: 'Return',
                  type: 'line',
                  id: 'percentage',
                  color: darkMode ? '#7BEFFF' : '#7E51FD',
                },
                {
                  value: `${token} Price`,
                  type: 'line',
                  id: 'price',
                  color: darkMode ? '#19EE94' : '#008E97',
                },
              ]}
              details={true}
              lineCount={2}
              fill={true}
            />
          </Fragment>
        )}
        {activeTab === 'Backtest' && (
          <Fragment>
            <BackTestAnlysisMini
              details={true}
              color={{ percentage: darkMode ? '#19EE94' : '#7E51FD' }}
              data={
                backtestAnalysisData &&
                  backtestAnalysisData[strategy.strategyId]
                  ? backtestAnalysisData[strategy.strategyId]
                  : []
              }
            />
          </Fragment>
        )}
        {activeTab === 'Investments' && (
          <Fragment>
            <UserPerformanceMini
              color={{ percentage: darkMode ? '#85DAE5' : '#7E51FD' }}
              data={performanceList}
              token={'Token'}
              payload={[
                {
                  value: 'Return',
                  type: 'line',
                  id: 'percentage',
                  color: '#85DAE5',
                },
              ]}
            />
          </Fragment>
        )}
      </div>
      <div className="itemRow" style={{ justifyContent: 'space-between' }}>
        <Button
          variant="contained"
          size="small"
          text={'Deposit'}
          disabled={tvlGteCapacity || !walletConnected}
          onClick={(event) => clickDeposit(event)}
          style={{ width: '40%', padding: '3px' }}
        />
        <Button
          variant="info"
          size="small"
          text={'Withdraw'}
          disabled={!(walletConnected && holdings > 0)}
          onClick={(event) => clickWithdraw(event)}
          style={{ width: '40%', padding: '3px' }}
        />
      </div>
    </div>
  );
};
