import { useEffect, useMemo, useRef } from 'react';
import { StrategyInfo } from '../../../../../../constants/strategy/index';
import {
  useFetchAssetPrice,
  useFetchBacktestAnalysis,
  useFetchHomoraPerformace,
  useFetchUserGraphInfo,
} from '../../../../../../features/graph/useFetchGraphData';
import {
  calculate_timeframe_apy,
  getPerformanceData,
  getReturnData,
  TimeframeType,
} from '../../../../../../helpers/graph';
import {
  calculateThreshold,
  isTVLGreaterOrEqualToCapacity,
  profitsClassName,
  profitsDataParser,
  timestampToUTCString,
} from '../../../../../../helpers/utilities';
import { IStore, useStore } from '../../../../../../store';
import { useOpenModal } from '../../../../../../store/base/application/hooks';
import { ModalType } from '../../../../../../store/base/AppSlice';
import textContext from '../../../../../../textContext';
import { PositionInfo } from '../../../../../../types';
import { Button } from '../../../Button';
import { VaultItem } from '../../../Strategy';
import { BackTestAnlysis } from '../../../Strategy/LineGraph/BackTestAnlysis';
import { Return } from '../../../Strategy/LineGraph/Return';
import { UserPerformance } from '../../LineGraph/UserPerformance';
import './index.scss';
import { OverallInfo } from './OverallInfo';

interface PdnStrategyDetailsProps {
  strategyInfo: StrategyInfo;
  positionInfo?: PositionInfo;
  vaultTitle: string;
  scrollToReturn?: boolean;
  scrollToAnalysis?: boolean;
}
export const PdnStrategyDetails = ({
  strategyInfo,
  vaultTitle,
  scrollToReturn,
  scrollToAnalysis,
  positionInfo,
}: PdnStrategyDetailsProps) => {
  const returnRef = useRef(null);
  const analysisRef = useRef(null);
  const language = useStore((state) => state.appSlice.language);
  const isWalletConnected = useStore(
    (state) => state.chainSlice.isWalletConnected
  );
  const darkMode = useStore((state: IStore) => state.themeSlice.darkMode);

  const { data: backtestAnalysisData } = useFetchBacktestAnalysis();
  const { data: assetPrice } = useFetchAssetPrice();
  const { data: homoraPerformance } = useFetchHomoraPerformace();
  const { data: performance } = useFetchUserGraphInfo(
    positionInfo?.positionId ?? '-1'
  );

  const performanceList = getPerformanceData(performance, positionInfo?.equity);
  const returns = getReturnData(
    assetPrice,
    homoraPerformance,
    strategyInfo.strategyId,
  );
  const returnData = returns.inception;
  const token =
    returnData && returnData.length > 0
      ? returnData[returnData.length - 1].ticker
      : 'Token';

  const text = textContext[language]['investment'];
  const { invest } = text;

  const getFarmingPoolInfo = (tokens: any[], protocols: any[]) => {
    const tokenString =
      `${tokens[0].ticker} - ${tokens[1].ticker}`.toUpperCase();
    if (protocols.length === 2)
      return (
        <div className="farming-pool">
          <div>{`${protocols[0].name} ${tokenString}`}</div>
          <div>{`${protocols[1].name} ${tokenString}`}</div>
        </div>
      );
    else return <>{`${protocols[0].name} ${tokenString}`}</>;
  };

  const poolData = getFarmingPoolInfo(
    strategyInfo.tokens,
    strategyInfo.protocols
  );

  const vaultItem = (
    <VaultItem
      {...{
        title: vaultTitle,
        tokens: strategyInfo.tokens,
        protocols: strategyInfo.protocols,
        strategyType: strategyInfo.strategyType,
      }}
    />
  );

  const tvlGteCapacity = useMemo(
    () =>
      isTVLGreaterOrEqualToCapacity(
        strategyInfo.TVL,
        strategyInfo.capacity,
        strategyInfo.leverage
      ),
    [strategyInfo.TVL, strategyInfo.capacity, strategyInfo.leverage]
  );

  const profits_Text = useMemo(() => {
    const text_value =
      positionInfo && positionInfo.profits
        ? profitsDataParser(positionInfo.equity, positionInfo.profits)
        : '$--';
    return positionInfo && positionInfo.profits
      ? [profitsClassName(text_value, positionInfo.profits.profits), text_value]
      : ['', text_value];
  }, [positionInfo]);

  useEffect(() => {
    setTimeout(() => {
      if (scrollToReturn && returnRef && returnRef.current) {
        returnRef!.current!.scrollIntoView({ behavior: 'smooth' });
      }
      if (scrollToAnalysis && analysisRef && analysisRef.current) {
        analysisRef!.current!.scrollIntoView({ behavior: 'smooth' });
      }
    }, 100);
  }, [returnRef, analysisRef]);

  const handleInvest = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    e.stopPropagation();
    openInvestModal();
  };

  const handleWithdraw = (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ) => {
    e.stopPropagation();
    openWithdrawlModal();
  };

  const modalProps = {
    strategyInfo,
    positionInfo,
    vaultItem,
    title: invest.PDN.title,
  };

  const openInvestModal = useOpenModal(ModalType.INVEST, {
    invest: true,
    ...modalProps,
  });

  const openWithdrawlModal = useOpenModal(ModalType.INVEST, {
    invest: false,
    ...modalProps,
  });

  return (
    <div className="pdn-strategy-details">
      <div className="pdn-strategy-details-title">
        <div className="headline-2">{`${invest.PDN.title}(${strategyInfo.leverage}X)`}</div>
        <div className="details-row">
          {vaultItem}
          <div className="action-button-group">
            <Button
              variant="contained"
              size="medium"
              text={'Deposit'}
              onClick={(event) => handleInvest(event)}
              disabled={tvlGteCapacity || !isWalletConnected}
            ></Button>
            <Button
              className="action-withdraw"
              variant="info"
              size="medium"
              text={'Withdraw'}
              disabled={
                !(
                  isWalletConnected &&
                  positionInfo &&
                  Number(positionInfo.equity) > 0
                )
              }
              onClick={(event) => handleWithdraw(event)}
            ></Button>
          </div>
        </div>
      </div>
      <div className="pdn-strategy-details-content">
        <div className="subtitle-1" style={{ paddingBottom: '12px' }}>
          Strategy Overview
        </div>
        <div className="body-1">{invest.PDN.description}</div>
        <OverallInfo
          holdings={Number(positionInfo?.equity)}
          profits={profits_Text}
          apy_7days={
            returns?.week
              ? calculate_timeframe_apy(returns.week, TimeframeType.WEEK)
              : null
          }
          leverage={strategyInfo.leverage}
          date={
            returnData && returnData.length > 0
              ? timestampToUTCString(
                  returnData[0]!.timestamp_sec,
                  'DayMonthYear'
                )
              : ''
          }
          tvl={Number(strategyInfo.TVL)}
          capacity={Number(strategyInfo.capacity)}
          threshold={calculateThreshold(
            strategyInfo.priceMovementThreshold,
            strategyInfo.leverage
          )}
          pool={poolData}
        />
        {positionInfo && Number(positionInfo.equity) > 0 && (
          <UserPerformance
            color={{ percentage: darkMode ? '#85DAE5' : '#7E51FD' }}
            data={performanceList}
            token={''}
            payload={[
              {
                value: 'Change',
                type: 'line',
                id: 'percentage',
                color: '#85DAE5',
              },
            ]}
            style={{ marginTop: '16px' }}
          />
        )}
        <div ref={returnRef}>
          <Return
            color={{ percentage: darkMode ? '#7BEFFF' : '#7E51FD', price: darkMode ? '#19EE94' : '#1A9A64' }}
            data={returns}
            token={token}
            payload={[
              {
                value: 'Return',
                type: 'line',
                id: 'percentage',
                color: darkMode ? '#7BEFFF' : '#7E51FD',
              },
              {
                value: `${token} Price`,
                type: 'line',
                id: 'price',
                color: darkMode ? '#19EE94' : '#1A9A64',
              },
            ]}
            style={{ marginTop: '16px' }}
          />
        </div>

        <div ref={analysisRef}>
          <BackTestAnlysis
            title={`${poolData.props.children} ${strategyInfo.leverage}X`}
            color={{ percentage: darkMode ? '#19EE94' : '#7E51FD' }}
            data={
              backtestAnalysisData &&
                backtestAnalysisData[strategyInfo.strategyId]
                ? backtestAnalysisData[strategyInfo.strategyId]
                : []
            }
            style={{ marginTop: '16px' }}
          />
        </div>
      </div>
    </div>
  );
};
