import { DropdownInfoBox } from '../../..';
import { Box, Modal, UserPerformanceMini } from '../../../../../..';
import { StrategyInfo } from '../../../../../../../../constants/strategy';
import { useFetchUserGraphInfo } from '../../../../../../../../features/graph/useFetchGraphData';
import { getPerformanceData } from '../../../../../../../../helpers/graph';
import { PositionInfo } from '../../../../../../../../types';
import { PdnStrategyDetails } from '../../../PdnStrategyDetails';

// Zustand Store
import { IStore, useStore } from '../../../../../../../../store';

export interface PerformanceProps {
  modalChildren: any;
  positionInfo?: PositionInfo;
  strategyInfo: StrategyInfo;
  vaultTitle: any;
}
export const UserPerformance = ({
  modalChildren,
  strategyInfo,
  vaultTitle,
  positionInfo,
}: PerformanceProps) => {
  const { data: performance } = useFetchUserGraphInfo(positionInfo!.positionId);
  const performanceList = getPerformanceData(performance, positionInfo?.equity);
  const darkMode = useStore((state: IStore) => state.themeSlice.darkMode);

  return (
    <>
      <DropdownInfoBox
        title={'My Investments'}
        viewDetails={{
          buttonText: 'View Details',
          modalChildren,
        }}
        first
      >
        <Modal
          modalClassName="modal-pdn-strategy-details"
          trigger={
            <Box
              className="line-graph-box"
              style={{ margin: '12px 16px 16px', padding: '0px' }}
            >
              <UserPerformanceMini
                color={{ percentage: darkMode ? '#85DAE5' : '#7E51FD' }}
                data={performanceList}
                token={'Token'}
                payload={[
                  {
                    value: 'Return',
                    type: 'line',
                    id: 'percentage',
                    color: '#85DAE5',
                  },
                ]}
              />
            </Box>
          }
        >
          <PdnStrategyDetails
            strategyInfo={strategyInfo}
            positionInfo={positionInfo}
            vaultTitle={vaultTitle}
            scrollToReturn
          />
        </Modal>
      </DropdownInfoBox>
    </>
  );
};
